<template>
  <div id="AddBlogGrowth">
    <form v-if="!summited">
      <!--  -->
      <el-input placeholder="" v-model="AddGrowth.title" clearable value-format="yyyy-MM-dd"></el-input>
      <!--  -->
      <el-input type="textarea" autosize placeholder="" v-model="AddGrowth.content"></el-input>
      <!--  -->
      <el-button style="display:block;margin-top:10px;" type="success" @click.prevent="post"></el-button>
    </form>
  </div>
</template>

<script>
export default {
  name: "AddBlogGrowth",
  data() {
    return {
      AddGrowth: {
        title: "",
        content: ""
      }
    };
  },
  created() {},
  methods: {
    post: function() {
      this.$http
        .post(
          "https://wd8560417834unmeck.wilddogio.com/growth.json",
          this.AddGrowth
        )
        .then(function(data) {
          // console.log(data);
          this.$router.push({ path: "/" });
          this.submitSuccessfully();
        });
    },
    submitSuccessfully() {
      this.$notify({
        title: "",
        message: "",
        type: "success"
      });
    }
  }
};
</script>